main {
    .campuses {
        padding-bottom: $measure--small4;
        @include media('>=small') {
            @include standard-box();
        }

        @include media('<small') {
            @include default-margin();
        }
    }

    .campuses__content {
        p {
            @include heading-font-features();
            @include font-family('heading');
            @include betatype();
        }
    }

    .campuses__links {
        width: 100%;

        &.campuses--stacked {
            .campuses__links__cards {
                grid-template-columns: repeat(1, 1fr);
            }
        }

        .campuses__links__cards {
            list-style: none;
            margin-left: initial;
            margin-bottom: initial;
            max-width: inherit;

            @include media('>=tablet') {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: $measure--small2;
                grid-row-gap: $measure--small2;
            }

            li {
                margin-top: initial;

                @include media('<=between') {
                    margin-bottom: $measure--basic;
                    max-width: initial;
                }

                .campuses__links__anchor {
                    outline-offset: $measure--quarter;
                    display: block;
                    border-bottom: 0;

                    @include media('<=between') {
                        max-width: initial;
                    }

                    &:hover,
                    &:focus {
                        .campuses__links__card--image:after {
                            opacity: 0;
                            transition: opacity $transition-duration ease-in;
                        }
                    }
                }
            }

            li:nth-child(1) {
                .campuses__links__card--label__heading-icon {
                    background: $color-brand-primary;
                }
            }
            li:nth-child(2) {
                .campuses__links__card--label__heading-icon {
                    background: $color-orange50;
                }
            }
            li:nth-child(3) {
                .campuses__links__card--label__heading-icon {
                    background: $color-teal60;
                }
            }
        }

        .campuses__links__card {
            position: relative;
        }

        .campuses__links__card--image {
            line-height: 0;

            &::after {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                background-image: linear-gradient(
                    0deg,
                    rgba(0, 0, 0, 0.5),
                    transparent
                );
                opacity: 1;
                transition: opacity $transition-duration ease-in;
            }
        }

        figure {
            margin-top: 0;
        }

        .campuses__links__card--label {
            position: absolute;
            background-color: $color-purewhite;
            color: $color-nearblack;
            bottom: $measure--small1;
            left: $measure--small1;

            .campuses__links__card--label__heading {
                display: inline-flex;
                margin-bottom: initial;
                text-wrap: wrap;
                margin-top: 0;
            }

            .campuses__links__card--label__heading-text {
                padding: $measure--half;
                @include betatype();
            }

            .campuses__links__card--label__heading-icon {
                color: $color-purewhite;
                padding: $measure--half $measure--basic;
                line-height: 0;
                display: flex;
                flex-direction: column;
                justify-content: center;
            }
        }
    }
}
